.App {
  text-align: center;
}
#krshn{
  width: 50vw;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.rock{
  background-color: yellow;
  color: red;
  font-style: italic;
  font-weight: 400;
  padding: 10px;
  font-size: 19px;
  border-radius: 15px;
  margin: 9px;
}
#bthn{
  display: flex;
  margin-top: 20px;
  justify-content: center;
  align-items: center;
}
#bthn a{
  text-decoration: none;
}
.mock{
  margin-top: 10px;
}

#named{
  font-size: 28px;
  color: red;
  padding: 10px;

  background-color: yellow;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
h1 {
  font-size: 30px;
  white-space: nowrap;
  overflow: hidden;
  margin: 5px;
  animation: typewriter 2s steps(13) infinite alternate,
      blink 800ms steps(13) infinite normal;
  border-right: 5px solid black;
}
@keyframes typewriter {
  from {
      width: 0%;
  }

  to {
      width: 50%;
  }
}

@keyframes blink {
  from {
      border-color: black;
  }

  to {
      border-color: transparent;
  }
}
